body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}






.topnav {
  background-color: #333;
  overflow: hidden;

  margin: 0 0 0 20px;
  height: 2.15em;
  font-size: 96%;
  clear: left;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Add a color to the active/current link */
.topnav a.active {
  background-color: #04AA6D;
  color: white;
}

div #site-name {
  float: left;
  width: 65%;
  font-size: 76%;
  padding: 13px 0 10px 10px;
}

div #admin-tools {
  font-size: 86%;
  padding: 2px 0 0;
  width: 300px;
  margin: 5px 0 0;
  float: right;
}

div #nav {
  margin: 0 0 0 20px;
  height: 2.15em;
  font-size: 96%;
  clear: left;
}

div #nav li {
  float: left;
  background: url(./images/nav_left_both.gif) no-repeat 0 -150px;
  margin: 0;
  padding: 0 0 0 9px;
  border-bottom: 1px solid #FFFFFF;
}